const carruselImagenes= ()=>{
    new Glider(document.querySelector('.carousel-list'), {
        slidesToShow: 1,
        slidesToScroll: 1,
        draggable: true,
        dots: '.tabuladores',
        responsive: [
            {
              // screens greater than >= 775px
              breakpoint: 600,
              settings: {
                // Set to `auto` and provide item width to adjust to viewport
                slidesToShow: 2,
                slidesToScroll: 2,
                itemWidth: 150,
                duration: 0.25
              }
            },{
              // screens greater than >= 1024px
              breakpoint: 800,
              settings: {
                slidesToShow: 4,
                slidesToScroll: 4,
                itemWidth: 150,
                duration: 0.25
              }
            }
          ]

      });
};
carruselImagenes();

const carruselOpiniones = ()=>{
  new Glider(document.querySelector('.opiniones--container'), {
    slidesToShow: 1,
    slidesToScroll: 1,
    dots: '.opiniones--tabulador',
    responsive: [
        {
          // screens greater than >= 775px
          breakpoint: 600,
          settings: {
            // Set to `auto` and provide item width to adjust to viewport
            slidesToShow: 2,
            slidesToScroll: 2,
            itemWidth: 150,
            duration: 0.25
          }
        },{
          // screens greater than >= 1024px
          breakpoint: 800,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            itemWidth: 150,
            duration: 0.25
          }
        }
      ]

  });
};
carruselOpiniones();